"use client"

// src/app/about/page.jsx (or similar)
import React from 'react';

// NOTE: In a real Next.js app, you would use Image component for optimization
// and store styles in a module file (e.g., './About.module.css').
const teamData = [
    { id: 1, name: 'Alex', title: 'CEO', image: '/placeholder-member-1.jpg' },
    { id: 2, name: 'Sarah', title: 'Lead Dev', image: '/placeholder-member-2.jpg' },
    { id: 3, name: 'Max', title: 'Designer', image: '/placeholder-member-3.jpg' },
    { id: 4, name: 'Jens', title: 'PM', image: '/placeholder-member-4.jpg' },
    { id: 5, name: 'Lia', title: 'Copywriter', image: '/placeholder-member-5.jpg' },
    { id: 6, name: 'Ben', title: 'Engineer', image: '/placeholder-member-6.jpg' },
    { id: 7, name: 'Eva', title: 'Analyst', image: '/placeholder-member-7.jpg' },
    { id: 8, name: 'Toby', title: 'QA', image: '/placeholder-member-8.jpg' },
    { id: 9, name: 'Mia', title: 'Marketing', image: '/placeholder-member-9.jpg' },
    { id: 10, name: 'Leo', title: 'Product', image: '/placeholder-member-10.jpg' },
];

const teamMemberStyle = {
    textAlign: 'center',
    marginBottom: '20px',
};
const teamImageStyle = {
    width: '100%',
    aspectRatio: '1 / 1',
    objectFit: 'cover',
    borderRadius: '4px',
};
const memberNameStyle = {
    color: 'white',
    fontWeight: '600',
    marginTop: '10px',
    marginBottom: '2px',
};
const memberTitleStyle = {
    color: '#999',
    fontSize: '0.9rem',
};

const TeamMember = ({ name, title, image }) => (
    <div style={teamMemberStyle}>
        <img src={image} alt={name} style={teamImageStyle} />
        <p style={memberNameStyle}>{name}</p>
        <p style={memberTitleStyle}>{title}</p>
    </div>
);


export default function AboutPage() {
  return (
    // 1. Main Container: Apply global dark theme
    <div className="mainContainer" style={{
        backgroundColor: '#000',
        color: '#fff',
        fontFamily: 'Arial, sans-serif',
        lineHeight: '1.6',
    }}>

      {/* --- Global Styles via <style> tag (for demonstration) --- */}
      <style global jsx>{`
        /* Reset and Base Styles */
        body { margin: 0; padding: 0; background-color: #000; }
        h1, h2, h3, p { margin: 0; padding: 0; }
        .contentWrapper { max-width: 1200px; margin: 0 auto; padding: 0 20px; }
        
        /* Utility Classes (Matching Design Aesthetic) */
        .largeTitle { 
            font-size: 8rem; 
            font-weight: 100; 
            color: #333; /* For the "ghosted" large titles */
            text-align: center; 
            margin-bottom: -50px; 
            padding-top: 50px;
            text-transform: uppercase;
        }
        .sectionSubHeadline { 
            font-size: 1.5rem; 
            font-weight: 300; 
            color: #fff; 
            text-align: center; 
            margin-bottom: 80px;
        }
        .statNumber { 
            font-size: 4rem; 
            font-weight: 700; 
            color: #fff; 
            margin-bottom: 5px; 
        }
        .statLabel { 
            font-size: 1rem; 
            color: #999; 
            text-transform: uppercase; 
        }

        /* --- RESPONSIVENESS (Media Queries) --- */
        @media (max-width: 1024px) {
            .largeTitle { font-size: 6rem; margin-bottom: -30px; }
            .statNumber { font-size: 3rem; }
        }
        @media (max-width: 600px) {
            .largeTitle { font-size: 4rem; margin-bottom: -10px; }
            .sectionSubHeadline { font-size: 1.2rem; margin-bottom: 40px; }
            .statNumber { font-size: 2.5rem; }
        }
      `}</style>
      
      {/* 2. HEADER/NAV (Placeholder) */}
      <header className="contentWrapper" style={{ padding: '20px', display: 'flex', justifyContent: 'space-between' }}>
        <div style={{ fontSize: '1.5rem', fontWeight: 'bold' }}>UTILITY</div>
        {/* Nav links here */}
      </header>

      {/* 3. HERO SECTION */}
      <section className="heroSection contentWrapper" style={{ padding: '100px 20px', display: 'flex', alignItems: 'center', flexWrap: 'wrap' }}>
        <div style={{ flex: '1 1 50%', minWidth: '300px' }}>
          <h1 style={{ fontSize: '3rem', fontWeight: 'bold', marginBottom: '20px' }}>
            Accelerated with <span style={{ color: '#00ccff' }}>impact</span> and <span style={{ color: '#00ccff' }}>results</span>
          </h1>
          <p style={{ color: '#ccc', marginBottom: '30px' }}>
            We are a collective of driven creators, strategists, and technologists focused on building digital products that matter.
          </p>
          <button style={{ padding: '10px 30px', backgroundColor: '#fff', color: '#000', border: 'none', cursor: 'pointer', fontWeight: 'bold' }}>
            UTILITY
          </button>
        </div>
        <div style={{ flex: '1 1 40%', minWidth: '300px', paddingLeft: '50px' }}>
          {/* Placeholder for the team meeting image */}
          <img src="/placeholder-hero.jpg" alt="Team meeting" style={{ maxWidth: '100%', height: 'auto', borderRadius: '8px' }} />
        </div>
      </section>

      {/* 4. ABOUT US / STATS SECTION */}
      <section className="aboutSection" style={{ padding: '80px 0', overflow: 'hidden' }}>
        <h1 className="largeTitle">About Us</h1>
        <div className="contentWrapper" style={{ display: 'flex', alignItems: 'center', flexWrap: 'wrap', marginTop: '50px' }}>
          
          {/* STATS */}
          <div style={{ flex: '1 1 30%', minWidth: '250px', display: 'flex', justifyContent: 'space-around', paddingRight: '50px', boxSizing: 'border-box' }}>
            <div style={{ textAlign: 'center' }}>
              <p className="statNumber">150</p>
              <p className="statLabel">Awards Won</p>
            </div>
            <div style={{ textAlign: 'center' }}>
              <p className="statNumber">100+</p>
              <p className="statLabel">Clients Served</p>
            </div>
          </div>

          {/* IMAGE */}
          <div style={{ flex: '1 1 60%', minWidth: '350px' }}>
             {/* Placeholder for the team collab image */}
            <img src="/placeholder-stats-team.jpg" alt="Team collaborating" style={{ maxWidth: '100%', height: 'auto', borderRadius: '8px' }} />
          </div>
        </div>
      </section>

      {/* 5. APPROACH & DATA POINTS SECTION */}
      <section className="approachSection contentWrapper" style={{ padding: '50px 20px', display: 'flex', flexWrap: 'wrap', gap: '50px' }}>
        
        {/* LEFT: APPROACH TEXT */}
        <div style={{ flex: '1 1 45%', minWidth: '300px' }}>
          <h3 style={{ fontSize: '2rem', fontWeight: '500', marginBottom: '15px' }}>
            We know our approach is simple.
          </h3>
          <p style={{ color: '#ccc' }}>
            Our method is iterative and human-centered, ensuring every product we create is not only technically sound but also resonates deeply with its users. Transparency and speed define our workflow.
          </p>
          {/* Placeholder for small top image/shape */}
        </div>
        
        {/* RIGHT: DATA POINTS GRID */}
        <div style={{ flex: '1 1 45%', minWidth: '300px', display: 'grid', gridTemplateColumns: 'repeat(3, 1fr)', gap: '20px' }}>
          {[
            { num: '12', label: 'Years of Experience' },
            { num: '25', label: 'Active Projects' },
            { num: '1', label: 'Office Location' },
          ].map((item, index) => (
            <div key={index}>
              <p className="statNumber" style={{ fontSize: '2.5rem' }}>{item.num}</p>
              <p className="statLabel" style={{ fontSize: '0.9rem' }}>{item.label}</p>
            </div>
          ))}
        </div>
      </section>
      
      {/* 6. PEOPLE SECTION */}
      <section className="peopleSection" style={{ padding: '80px 0' }}>
        <h1 className="largeTitle">People</h1>
        <h2 className="sectionSubHeadline">Driven by creativity. Focused on results.</h2>
        
        <div className="contentWrapper" style={{ 
            display: 'grid', 
            gridTemplateColumns: 'repeat(auto-fit, minmax(180px, 1fr))', /* Responsive grid */
            gap: '30px' 
        }}>
          {teamData.map(member => (
              <TeamMember key={member.id} {...member} />
          ))}
        </div>
      </section>

      {/* 7. CULTURE SECTION */}
      <section className="cultureSection" style={{ padding: '80px 0' }}>
        <h1 className="largeTitle">Culture</h1>
        <div className="contentWrapper">
            
            {/* CULTURE ROW 1 (Text Left, Image Right) */}
            <div style={{ display: 'flex', flexWrap: 'wrap', alignItems: 'center', marginBottom: '50px' }}>
                <div style={{ flex: '1 1 45%', minWidth: '300px', paddingRight: '50px' }}>
                    <h3 style={{ fontSize: '2rem', marginBottom: '15px' }}>Work smart, not hard.</h3>
                    <p style={{ color: '#ccc' }}>
                        We foster an environment where efficiency and focus are prioritized over long hours. Our team is trusted to manage their time and deliver exceptional quality.
                    </p>
                </div>
                <div style={{ flex: '1 1 45%', minWidth: '300px' }}>
                    <img src="/placeholder-culture-1.jpg" alt="Office space wide" style={{ maxWidth: '100%', height: 'auto', borderRadius: '8px' }} />
                </div>
            </div>

            {/* CULTURE ROW 2 (Image Left, Text Right) - Inferred from original design */}
            <div style={{ display: 'flex', flexWrap: 'wrap', alignItems: 'center', marginTop: '80px' }}>
                <div style={{ flex: '1 1 30%', minWidth: '250px', paddingRight: '50px' }}>
                    <img src="/placeholder-culture-2.jpg" alt="Office corner" style={{ maxWidth: '100%', height: 'auto', borderRadius: '8px' }} />
                </div>
                <div style={{ flex: '1 1 60%', minWidth: '300px', display: 'grid', gridTemplateColumns: 'repeat(2, 1fr)', gap: '30px' }}>
                    <div>
                        <h3 style={{ fontSize: '1.5rem', marginBottom: '10px' }}>Our vision is to exceed expectations.</h3>
                        <p style={{ color: '#ccc' }}>
                            We aim to set the gold standard in digital product development by continuously pushing creative and technical boundaries.
                        </p>
                    </div>
                    <div>
                        <h3 style={{ fontSize: '1.5rem', marginBottom: '10px' }}>Our mission is to build digital products.</h3>
                        <p style={{ color: '#ccc' }}>
                            Focused on utility and user experience, we translate client goals into powerful, accessible, and scalable web solutions.
                        </p>
                    </div>
                </div>
            </div>
            
        </div>
      </section>
      
      {/* 8. FOOTER */}
      <footer style={{ padding: '80px 0', borderTop: '1px solid #111', marginTop: '50px' }}>
        <div className="contentWrapper">
            <h1 style={{ fontSize: '6rem', fontWeight: '100', textAlign: 'center', marginBottom: '40px' }}>UTILITY</h1>
            
            <div style={{ display: 'flex', justifyContent: 'space-around', flexWrap: 'wrap', paddingBottom: '30px' }}>
                <div><h4 style={{ color: '#999', marginBottom: '10px' }}>Services</h4> {/* Links here */}</div>
                <div><h4 style={{ color: '#999', marginBottom: '10px' }}>Company</h4> {/* Links here */}</div>
                <div><h4 style={{ color: '#999', marginBottom: '10px' }}>Contact</h4> {/* Links here */}</div>
            </div>
            
            <p style={{ textAlign: 'center', fontSize: '0.8rem', color: '#555', paddingTop: '20px' }}>
                &copy; {new Date().getFullYear()} Utility Agency. All rights reserved.
            </p>
        </div>
      </footer>

    </div>
  );
}